import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Container, ListGroup, FormControl } from 'react-bootstrap';

const App = () => {
  const [users, setUsers] = useState([]);
  const [searchText, setSearchText] = useState('');

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const response = await axios.get('https://reqres.in/api/users?page=2');
        setUsers(response.data.data);
      } catch (error) {
        console.error('Error fetching users:', error);
      }
    };

    fetchUsers();
  }, []);

  const handleSearch = (event) => {
    setSearchText(event.target.value);
  };

  const filteredUsers = users.filter((user) =>
    user.first_name.toLowerCase().includes(searchText.toLowerCase())
  );

  return (
    <Container className="mt-4">
      <FormControl
        type="text"
        placeholder="Search by first name"
        value={searchText}
        onChange={handleSearch}
        className="mb-4"
      />
      <ListGroup>
        {filteredUsers.map((user) => (
          <ListGroup.Item key={user.id} className="d-flex align-items-center">
            <div className="mr-3">{user.id}</div>
            <img src={user.avatar} alt="Avatar" className="mr-3" style={{ width: 87, height: 87, borderRadius: '18%' }} />
            <div>{user.first_name}</div>
          </ListGroup.Item>
        ))}
      </ListGroup>
    </Container>
  );
};

export default App;
